export default class AppView {
  constructor(data) {
    this.data = data;
  }

  render(key) {
    // CREATE ELEMENTS
    const main = document.querySelector('.main');
    const views = [];
    let str;
    // MUST DO REFACTORING
    if (document.querySelector('.clip-list')) {
      const clipList = document.querySelector('.clip-list');
      const clipsInList = +(getComputedStyle(clipList).getPropertyValue('--clip-total'));
      clipList.style.setProperty('--clip-total', clipsInList + this.data.items.length);
      clipList.innerHTML += this.data.items.map((clip) => {
        views.push(clip.id.videoId);
        if (clip.snippet.channelTitle.length > 15) {
          str = `${clip.snippet.channelTitle.slice(0, 15)} ...`;
        } else {
          str = clip.snippet.channelTitle;
        }
        return `<li class="clip">
                    <div class="clip-inner">
                       <img class="clip-image" src=${clip.snippet.thumbnails.medium.url} width=${clip.snippet.thumbnails.medium.width}>
                       <div class="clip-title">
                          <a class="clip-link" target="_blank">${clip.snippet.title}</a>
                       </div>
                       <div class="clip-info">
                          <div class="clip-wrapper">
                              <div class="clip-icon clip-channel-title">${str}</div>
                              <div class="clip-icon clip-date">${clip.snippet.publishedAt.slice(0, 10)}</div>
                              <div class="clip-icon clip-views"></div>
                          </div>
                          <div class="clip-desc">${clip.snippet.description}</div>
                       </div>
                    </div>
                 </li>`;
      }).join('');
    } else {
      const content = document.createElement('ul');
      content.setAttribute('class', 'clip-list');
      content.style.setProperty('--clip-total', this.data.items.length);
      content.innerHTML = this.data.items.map((clip) => {
        views.push(clip.id.videoId);
        if (clip.snippet.channelTitle.length > 15) {
          str = `${clip.snippet.channelTitle.slice(0, 15)} ...`;
        } else {
          str = clip.snippet.channelTitle;
        }
        return `<li class="clip">
                      <div class="clip-inner">
                        <img class="clip-image" src=${clip.snippet.thumbnails.medium.url}>
                        <div class="clip-title">
                            <a class="clip-link" target="_blank">${clip.snippet.title}</a>
                        </div>
                        <div class="clip-info">
                            <div class="clip-wrapper">
                                <div class="clip-icon clip-channel-title">${str}</div>
                                <div class="clip-icon clip-date">${clip.snippet.publishedAt.slice(0, 10)}</div>
                                <div class="clip-icon clip-views"></div>
                            </div>
                            <div class="clip-desc">${clip.snippet.description}</div>
                        </div>
                      </div>
                  </li>`;
      }).join('');
      main.appendChild(content);
    }
    if (!document.querySelector('.navigator')) {
      const dots = document.createElement('div');
      dots.setAttribute('class', 'navigator');

      dots.innerHTML = `<div class="dot-before-prev dot"></div>
                        <div class="dot-prev dot"></div>
                        <div class="dot-current dot -active">1</div>
                        <div class="dot-next dot"></div>`;
      main.appendChild(dots);
    }
    // REQUEST FOR VIDEO STATICTICS
    fetch(`https://www.googleapis.com/youtube/v3/videos?${key}&id=${views.join(',')}&part=snippet,statistics`)
      .then((response) => {
				 return response.json();
			})
			.then((request) => {
        const links = document.querySelectorAll('.clip-link');
        const visitors = document.querySelectorAll('.clip-views');
        let j = 0;
        for (let i = (getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clip-total') - 15); i < links.length; i += 1) {
          links[i].setAttribute('href', `https://www.youtube.com/watch?v=${request.items[j].id}`);
          visitors[i].innerHTML = request.items[j].statistics.viewCount;
          j += 1;
        }
        j = 0;
      });
    return document.querySelector('.navigator');
  }
}
