import App from './controllers/App';

const app = new App();
app.startPage();

document.addEventListener('keydown', startProject);
document.querySelector('.search-btn').addEventListener('click', startProject);

function startProject(e) {
  const inp = document.querySelector('.search-input');
  // if we pressed on 'Enter' or 'Search'
  if ((e.keyCode === 13 && inp.value.length !== 0) || (e.target.classList.contains('search-btn') && inp.value.length !== 0)) {
    const request = inp.value;
    // If data is not loaded
    if (document.querySelector('.wrapper-error')) {
      app.clearError();
    }
    // If there is a new request, it is cleaned
    if (document.querySelector('.clip-list')) {
      document.querySelector('.main').removeChild(document.querySelector('.clip-list'));
      document.querySelector('.main').removeChild(document.querySelector('.navigator'));
      app.count = 1;
      app.pos = 0;
      app.clips = [];
    }
    // START SPINER
    app.spiner();
    // ASYNC
    setTimeout(() => {
      const promise = app.start(request);
      // confusing implementation :)
      promise
        .then((elem) => {
          if (elem === 'Error') {
            const restart = document.querySelector('.restart');
            restart.addEventListener('click', () => {
              window.location.reload();
            });
          } else {
            // Remove Spiner
            app.clearSpiner();
            const clipList = document.querySelector('.clip-list');
            const current = document.querySelector('.dot-current');
            const beforePrev = document.querySelector('.dot-before-prev');
            const prev = document.querySelector('.dot-prev');
            const next = document.querySelector('.dot-next');
            let isDown = false;
            let offset;
            let startX;
            let endX;
            let accumulate = 0;

            clipList.style.transition = 'transform 0.5s';

            // SLIDER WITH BUTTONS
            elem.addEventListener('click', (event) => {
              if (event.target.classList.contains('dot-next')) {
                app.count += 1;
                app.pos += 1;
                // Add Tooltips
                const tooltip = app.tooltips();
                event.target.appendChild(tooltip);

                setTimeout(() => {
                  event.target.removeChild(tooltip);
                  event.target.classList.add('border');
                  if (app.count === 2) {
                    prev.classList.add('dot-active');
                  }
                  if (app.count === 3) {
                    beforePrev.classList.add('dot-active');
                  }
                  prev.classList.remove('border');
                  beforePrev.classList.remove('border');
                  clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                  current.innerHTML = app.count;
                  // DOWNLOAD A NEW PORTION VIDEO
                  if (app.count % 3 === 0 && app.clips.indexOf(app.count) === -1) {
                    app.start(document.querySelector('.search-input').value);
                    app.clips.push(app.count);
                  }
                }, 200);
              } else if (event.target.classList.contains('dot-prev')) {
                app.pos -= 1;
                app.count -= 1;
                const tooltip = app.tooltips();
                event.target.appendChild(tooltip);

                setTimeout(() => {
                  event.target.removeChild(tooltip);
                  event.target.classList.add('border');
                  if (app.count === 2) {
                    beforePrev.classList.remove('dot-active');
                    event.target.classList.add('border');
                  }
                  if (app.count === 1) {
                    event.target.classList.remove('dot-active');
                    event.target.classList.remove('border');
                  }
                  if (app.count > 2) {
                    event.target.classList.add('border');
                  }
                  next.classList.remove('border');
                  beforePrev.classList.remove('border');
                  clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                  current.innerHTML = app.count;
                }, 200);
              } else if (event.target.classList.contains('dot-before-prev')) {
                app.pos -= 2;
                app.count -= 2;
                const tooltip = app.tooltips();
                event.target.appendChild(tooltip);

                setTimeout(() => {
                  event.target.removeChild(tooltip);
                  event.target.classList.add('border');
                  if (app.count === 2) {
                    event.target.classList.remove('dot-active');
                    event.target.classList.remove('border');
                    prev.classList.add('border');
                  }
                  if (app.count === 1) {
                    event.target.classList.remove('dot-active');
                    event.target.classList.remove('border');
                    prev.classList.remove('dot-active');
                  }
                  if (app.count > 2) {
                    event.target.classList.add('border');
                  }
                  next.classList.remove('border');
                  beforePrev.classList.remove('border');
                  clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                  current.innerHTML = app.count;
                }, 200);
              }
              clipList.style.transition = 'transform 0.5s';
            });

            // SLIDER WITH DRAG

            // MOUSEDOWN
            document.addEventListener('mousedown', (event) => {
              isDown = true;
              startX = event.pageX;
              clipList.style.transition = '';
            });
            // MOUSELEAVE
            document.addEventListener('mouseleave', () => {
              isDown = false;
            });
            // MOUSEMOVE
            document.addEventListener('mousemove', (event) => {
              if (!isDown) return;
              event.preventDefault();
              endX = event.pageX;
              offset = endX - startX;
              accumulate = offset + (-document.documentElement.clientWidth * (app.count - 1));
              clipList.style.transform = `translateX(${accumulate}px)`;
            });
            // MOUSEUP
            document.addEventListener('mouseup', () => {
              isDown = false;
              if (accumulate > 0) {
                clipList.style.transform = 'translateX(0px)';
              } else if (startX - endX > 130) {
                app.count += 1;
                app.pos += 1;
                clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                if (app.count === 2) {
                  prev.classList.add('dot-active');
                }
                if (app.count === 3) {
                  beforePrev.classList.add('dot-active');
                }
                if (prev.classList.contains('border')) {
                  prev.classList.remove('border');
                }
                next.classList.add('border');
                current.innerHTML = app.count;
              } else if (endX - startX > 130 && app.count > 0) {
                app.count -= 1;
                app.pos -= 1;
                clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                if (app.count === 2) {
                  beforePrev.classList.remove('dot-active');
                  prev.classList.add('border');
                }
                if (app.count === 1) {
                  prev.classList.remove('dot-active');
                  prev.classList.remove('border');
                }
                if (app.count > 2) {
                  prev.classList.add('border');
                }
                next.classList.remove('border');
                current.innerHTML = app.count;
              } else {
                clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
              }
              if (app.count % 3 === 0 && app.clips.indexOf(app.count) === -1) {
                app.start(document.querySelector('.search-input').value);
                app.clips.push(app.count);
              }
              startX = undefined;
              endX = undefined;
              clipList.style.transition = 'transform 0.5s';
            });

            // TOUCHSTART
            clipList.addEventListener('touchstart', (event) => {
              startX = event.touches[0].clientX;
            });
            // TOUCHMOVE
            clipList.addEventListener('touchmove', (event) => {
              clipList.style.transition = '';
              endX = event.touches[0].clientX;
              offset = endX - startX;
              accumulate = offset + (-document.documentElement.clientWidth * (app.count - 1));
              clipList.style.transform = `translateX('${accumulate}px)`;
            });
            // TOUCHEND
            clipList.addEventListener('touchend', () => {
              if (accumulate > 0) {
                clipList.style.transform = 'translateX(0px)';
              } else if (startX - endX > 30) {
                app.count += 1;
                app.pos += 1;
                clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                if (app.count === 2) {
                  prev.classList.add('dot-active');
                }
                if (app.count === 3) {
                  beforePrev.classList.add('dot-active');
                }
                if (prev.classList.contains('border')) {
                  prev.classList.remove('border');
                }
                next.classList.add('border');
                current.innerHTML = app.count;
              } else if (endX - startX > 30 && app.count > 0) {
                app.count -= 1;
                app.pos -= 1;
                clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
                if (app.count === 2) {
                  beforePrev.classList.remove('dot-active');
                  prev.classList.add('border');
                }
                if (app.count === 1) {
                  prev.classList.remove('dot-active');
                  prev.classList.remove('border');
                }
                if (app.count > 2) {
                  prev.classList.add('border');
                }
                next.classList.remove('border');
                current.innerHTML = app.count;
              } else {
                clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
              }
              startX = undefined;
              endX = undefined;
              clipList.style.transition = 'transform 0.5s';
            });
            window.addEventListener('resize', () => {
              clipList.style.transform = `translateX(${-document.documentElement.clientWidth * app.pos}px)`;
            });
          }
        });
    }, 2000);
  }
}
