import AppModel from '../models/AppModel';
import AppView from '../views/AppViews';

export default class App {
  constructor() {
    this.state = {
      address: 'https://www.googleapis.com/youtube/v3/',
      search: 'search?',
      key: 'key=AIzaSyCbUr_IN4S6BJTZwnJ7dEdI6kCFZNspe4A',
      type: '&type=video',
      id: '&id=',
      token: '&pageToken=',
      nextToken: '',
      part: '&part=snippet',
      chunk: '&maxResults=15',
      query: '&q=',
    };
    this.pos = 0;
    this.count = 1;
    this.clips = [];
  }

  startPage() {
    const wrapper = document.createElement('div');
    const viewport = document.createElement('meta');
    const charset = document.querySelector('meta');

    viewport.setAttribute('name', 'viewport');
    viewport.setAttribute('content', 'width=device-width, initial-scale=1');

    wrapper.setAttribute('class', 'wrapper');
    wrapper.innerHTML = `<header class="header">
                              <label class="magnifier search-label" for="search-input">
                                <input class="search-input" id="search-input" type="text" placeholder="What do you want to find?">
                                <button class="search-btn">Search</button>
                              </label>
                           </header>
                           <main class="main"></main>`;
    document.head.insertBefore(viewport, charset);
    document.body.appendChild(wrapper);
  }

  spiner() {
    const wrapperSpiner = document.createElement('div');
    wrapperSpiner.setAttribute('class', 'wrapper-spiner');
    wrapperSpiner.innerHTML = `<div id="floatingCirclesG">
                                          <div class="f_circleG" id=frotateG_01></div>
                                          <div class="f_circleG" id=frotateG_02></div>
                                          <div class="f_circleG" id=frotateG_03></div>
                                          <div class="f_circleG" id=frotateG_04></div>
                                          <div class="f_circleG" id=frotateG_05></div>
                                          <div class="f_circleG" id=frotateG_06></div>
                                          <div class="f_circleG" id=frotateG_07></div>
                                          <div class="f_circleG" id=frotateG_08></div>
                                     </div>
                                     <div class="loading">Loading</div>`;
    document.querySelector('.wrapper').appendChild(wrapperSpiner);
  }

  clearSpiner() {
    const wrapper = document.querySelector('.wrapper');
    const spiner = document.querySelector('.wrapper-spiner');
    wrapper.removeChild(spiner);
  }

  error() {
    const error = document.createElement('div');
    const wrapper = document.querySelector('.wrapper');

    error.setAttribute('class', 'wrapper-error');
    error.innerHTML = `<div class="error">
                          <span class="round"></span>
                          <span class="error-text">Ooops... Something went wrong</span>
                       </div>
                       <h2 class="error-title"><span class="restart">Try again</span>`;

    wrapper.removeChild(document.querySelector('.wrapper-spiner'));
    wrapper.appendChild(error);
    return 'Error';
  }

  clearError() {
    const wrapper = document.querySelector('.wrapper');
    const error = document.querySelector('.wrapper-error');
    wrapper.removeChild(error);
  }

  tooltips() {
    const note = document.createElement('div');
    note.setAttribute('class', 'tooltip');
    note.innerHTML = this.count;
    return note;
  }

  async start(request) {
    const { address, search, key, type, token, nextToken, part, chunk, query } = this.state;
    let searching;
    if (document.querySelector('.clip-list') && this.count % 3 === 0 && this.clips.indexOf(this.count) === -1) {
      searching = `${address}${search}${key}${type}${part}${token}${nextToken}${chunk}${query}${request}`;
    } else {
      searching = `${address}${search}${key}${type}${part}${chunk}${query}${request}`;
    }
    const model = new AppModel(searching);
    const data = await model.getClips();
    // If we receive "Error" in response
    if (data === 'Error') {
      return this.error();
    }
    const view = new AppView(data);
    view.render(this.state.key);
    this.state.nextToken = data.nextPageToken;
    return new Promise((resolve) => {
      if (document.querySelector('.navigator')) {
        const dots = document.querySelector('.navigator');
        return resolve(dots);
      }
    });
  }
}
